import React from "react";

const RoutsTableRows = ({ rowsData }) => {
  return rowsData.map((data, index) => {
    const { customer, address, date, edit, sel, prio, payroll } = data;
    return (
      <tr key={index}>
        <td>
          <span>{customer}</span>{" "}
        </td>
        <td>
          <span>{address}</span>{" "}
        </td>
        <td>
          <span>{payroll}</span>{" "}
        </td>
      </tr>
    );
  });
};

export default RoutsTableRows;
