import React, { useState, useEffect, useRef } from "react";
import { Link, useLocation, useNavigate } from "react-router-dom";
// import avatar from "./avatar.png";
import userlogo from "./userlogo.svg";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faAddressCard,
  faListAlt,
  faMap,
  faFlag,
  faDotCircle,
  faCalendarAlt,
  faUser,
  faSun,
} from "@fortawesome/free-regular-svg-icons";

const AddressCard = (
  <FontAwesomeIcon className="icon" icon={faAddressCard} size="lg" />
);
const ListAlt = <FontAwesomeIcon className="icon" icon={faListAlt} size="lg" />;
const Map = <FontAwesomeIcon className="icon" icon={faMap} size="lg" />;
const Flag = <FontAwesomeIcon className="icon" icon={faFlag} size="lg" />;
const DotCircle = (
  <FontAwesomeIcon className="icon" icon={faDotCircle} size="lg" />
);
const CalendarAlt = (
  <FontAwesomeIcon className="icon" icon={faCalendarAlt} size="lg" />
);
const User = <FontAwesomeIcon className="icon" icon={faUser} size="lg" />;
const Sun = <FontAwesomeIcon className="icon" icon={faSun} size="lg" />;

const Sidebar = (props) => {
  const uploadedImage = useRef(null);
  const imageUploader = useRef(null);

  const handleImageUpload = (e) => {
    const [file] = e.target.files;
    if (file) {
      const reader = new FileReader();
      const { current } = uploadedImage;
      current.file = file;
      reader.onload = (e) => {
        current.src = e.target.result;
        // console.log(e.target.result);
        localStorage.setItem('photo', e.target.result);
      };
      reader.readAsDataURL(file);
    }

  };

  const email = localStorage.getItem("email");
  const photo = localStorage.getItem("photo");

  // const baseURL = process.env.PUBLIC_URL;
  const location = useLocation();
  // console.log(location.pathname);
  const navigate = useNavigate();

  const [showm, setshowm] = useState(false);
  const [settClick, setSettClick] = useState(false);

  const showmen = () => {
    let chshow = !showm;
    setshowm(chshow);
    setSettClick(true);
  };

  useEffect(() => {
    if (
      (location.pathname === "/users" ||
        location.pathname === "/drivers" ||
        location.pathname === "/locations") &&
      settClick === false
    ) {
      setshowm(true);
    }
    if (photo) {
      uploadedImage.current.src = photo;
    }
  });

  const logout = (e) => {
    e.preventDefault();
    // Submit to backend API with Axios/fetch
    // Implement your backend logic here.
    localStorage.removeItem("isloged");
    localStorage.removeItem("email");
    // window.location = baseURL + "#/login";
    navigate("/login");
  };

  return (
    <>
      <div className="col-auto col-md-3 col-xl-2 px-sm-2 px-0 bg-dark min-vh-100 noprint">
        <div className="d-flex flex-column align-items-center px-3 pt-2 text-white min-vh-100 justify-content-between position-fixed">
          <div className="dropdown my-3">
            <Link
              to="/"
              className="d-flex align-items-center justify-content-center pb-5 mb-md-0 me-md-auto text-white text-decoration-none"
            >
              <span className="fs-5 d-none d-md-inline">
                Delivery Route Planner
              </span>
            </Link>
            <div className="d-flex justify-content-center">
              <form>
                <input
                  type="file"
                  accept="image/*"
                  onChange={handleImageUpload}
                  ref={imageUploader}
                  style={{
                    display: "none",
                  }}
                />
              </form>
              <img
                src={userlogo}
                alt={"user logo"}
                width="50"
                height="50"
                className="rounded-circle mx-auto"
                ref={uploadedImage}
                onClick={() => imageUploader.current.click()}
              ></img>
            </div>
            <Link
              to="#"
              className="d-flex align-items-center justify-content-center text-white text-decoration-none dropdown-toggle"
              id="dropdownUser1"
              data-bs-toggle="dropdown"
              aria-expanded="false"
            >
              <span className="d-none d-sm-block mx-1">{email}</span>
            </Link>
            <ul className="dropdown-menu dropdown-menu-dark text-small shadow">
              <li>
                <Link className="dropdown-item" to="#" onClick={logout}>
                  Sign out
                </Link>
              </li>
            </ul>
          </div>
          <div className="pb-3">
            <ul
              className="nav nav-pills flex-column mb-sm-auto mb-0 align-items-center align-items-sm-start my-2"
              id="menu"
            >
              <li
                className={
                  location.pathname === "/destinations" ||
                  location.pathname === "/"
                    ? "d-none"
                    : "nav-item my-1"
                }
              >
                <Link
                  to="/destinations"
                  className="align-middle px-0"
                  title="Create New Route"
                >
                  <i>{AddressCard}</i>
                </Link>
                &nbsp;
                <span className="ms-1 d-none d-md-inline">
                  Create New Route
                </span>
              </li>

              <li
                className={
                  location.pathname === "/routes" ? "d-none" : "nav-item my-1"
                }
              >
                <Link to="/routes" className="align-middle px-0" title="Routes">
                  <i>{Map}</i>
                </Link>
                &nbsp;
                <span className="ms-1 d-none d-md-inline">Routes</span>
              </li>
              <li
                className={
                  location.pathname === "/log" ? "d-none" : "nav-item my-1"
                }
              >
                <Link to="/log" className="align-middle px-0" title="Log">
                  <i>{CalendarAlt}</i>
                </Link>
                &nbsp;
                <span className="ms-1 d-none d-md-inline">Log</span>
              </li>
              <li>
                <button
                  className="align-middle px-0 btn btn-link"
                  title="Settings"
                  onClick={showmen}
                >
                  <i>{Sun}</i>
                </button>
                &nbsp;
                <span className="ms-1 d-none d-md-inline">Settings</span>
              </li>
              <div
                className={
                  showm ? "text-warning text-decoration-none" : "d-none"
                }
              >
                <li
                  className={
                    location.pathname === "/users" ? "d-none" : "nav-item my-1"
                  }
                >
                  <Link to="/users" className="align-middle px-0" title="Users">
                    <i>{User}</i>
                  </Link>
                  &nbsp;
                  <span className="ms-1 d-none d-sm-inline">Users</span>
                </li>
                <li
                  className={
                    location.pathname === "/drivers"
                      ? "d-none"
                      : "nav-item my-1"
                  }
                >
                  <Link
                    to="/drivers"
                    className="align-middle px-0"
                    title="Drivers"
                  >
                    <i>{DotCircle}</i>
                  </Link>
                  &nbsp;
                  <span className="ms-1 d-none d-sm-inline">Drivers</span>
                </li>
                <li
                  className={
                    location.pathname === "/locations"
                      ? "d-none"
                      : "nav-item my-1"
                  }
                >
                  <Link
                    to="/locations"
                    className="align-middle px-0"
                    title="Locations"
                  >
                    <i>{Flag}</i>
                  </Link>
                  &nbsp;
                  <span className="ms-1 d-none d-sm-inline">Locations</span>
                </li>
              </div>
            </ul>
          </div>
        </div>
      </div>
    </>
  );
};

export default Sidebar;
