import React from "react";

const DestTableRows = ({
  rowsData,
  handleChange,
  editTableRows,
  selTableRows,
}) => {
  return rowsData.map((data, index) => {
    const { customer, address, date, edit, sel } = data;
    return (
      <tr key={index}>
        <td>
          <span className={edit === 1 ? "d-none" : null}>{customer}</span>
          <input
            type="text"
            value={customer}
            onChange={(evnt) => handleChange(index, evnt)}
            name="customer"
            className={edit === 0 ? "d-none" : "form-control"}
          />
        </td>
        <td>
          <span className={edit === 1 ? "d-none" : null}>{address}</span>
          <input
            type="text"
            value={address}
            onChange={(evnt) => handleChange(index, evnt)}
            name="address"
            className={edit === 0 ? "d-none" : "form-control"}
          />{" "}
        </td>
        <td>
          <button
            onClick={() => editTableRows(index, 1)}
            className={edit === 1 ? "d-none" : "btn btn-link"}
          >
            Edit
          </button>
          <button
            onClick={() => editTableRows(index, 0)}
            className={edit === 0 ? "d-none" : "btn btn-link"}
          >
            Enter
          </button>
        </td>
        <td>
          <input
            type="checkbox"
            checked={sel === 1}
            onChange={() => selTableRows(index, sel)}
          />
        </td>
      </tr>
    );
  });
};

export default DestTableRows;
