import React from "react";
import SelectOptions from "../Controls/SelectOptions";
import genArrInt from "../Helpers/Helpers";

const RGTableRows = ({
  rowsData,
  handleChange,
  editTableRows,
  selTableRows,
}) => {
  const arrPrior = genArrInt(rowsData.length);
  arrPrior.unshift("");
  return rowsData.map((data, index) => {
    const { customer, address, date, edit, sel, prio, payroll } = data;
    return (
      <tr key={index}>
        <td>
          <span className={edit === 1 ? "d-none" : null}>{customer}</span>
          <input
            type="text"
            value={customer}
            onChange={(evnt) => handleChange(index, evnt)}
            name="customer"
            className={edit === 0 ? "d-none" : "form-control"}
          />
        </td>
        <td>
          <span className={edit === 1 ? "d-none" : null}>{address}</span>
          <input
            type="text"
            value={address}
            onChange={(evnt) => handleChange(index, evnt)}
            name="address"
            className={edit === 0 ? "d-none" : "form-control"}
          />{" "}
        </td>
        <td>
          <button
x          onClick={() => editTableRows(index, 1)}
            className={edit === 1 ? "d-none" : "btn btn-link"}
          >
            Edit
          </button>
          <button
            onClick={() => editTableRows(index, 0)}
            className={edit === 0 ? "d-none" :"btn btn-link"}
          >
            Enter
          </button>
        </td>
        <td>
          <input
            type="checkbox"
            checked={sel === 1}
            onChange={() => selTableRows(index, sel)}
          />
        </td>
        <td>
          <select
            value={prio}
            onChange={(evnt) => handleChange(index, evnt)}
            name="prio"
          >
            <SelectOptions arrval={arrPrior}></SelectOptions>
          </select>
        </td>
        <td>
          <span className={edit === 1 ? "d-none" : null}>{payroll}</span>
          <input
            type="text"
            value={payroll}
            onChange={(evnt) => handleChange(index, evnt)}
            name="payroll"
            className={edit === 0 ? "d-none" : "form-control"}
          />{" "}
        </td>
      </tr>
    );
  });
};

export default RGTableRows;
