import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.bundle.min";
import { BrowserRouter, Routes, Route, HashRouter } from "react-router-dom"
import './App.css';
import Main from "./components/Main/Main"
import Auth from "./components/Auth/Auth"
import Dest from "./components/Dest/Dest"
import RG from "./components/RG/RG"
import Routs from "./components/Routs/Routs"
import Locations from "./components/Locations/Locations"
import Drivers from "./components/Drivers/Drivers"
import Users from "./components/Users/Users"
import Log from "./components/Log/Log"
import Settings from "./components/Settings/Settings"

const App = () => {
  const isloged = localStorage.getItem('isloged');
  const loged = (isloged && isloged === 'yes');
  // console.log(loged);
  const baseURL = process.env.PUBLIC_URL;
  return (
    <HashRouter>
      <Routes>
        <Route path="/" element={<Main />} />
        <Route path="/login" element={<Auth />} />
        {loged ? (
          <Route path="/destinations" element={<Dest />} />
        ) : (
          <Route path="/destinations" element={<Auth />} />
        )}
        {loged ? (
          <Route path="/routegenerator" element={<RG />} />
        ) : (
          <Route path="/routegenerator" element={<Auth />} />
        )}
        {loged ? (
          <Route path="/routes" element={<Routs />} />
        ) : (
          <Route path="/routes" element={<Auth />} />
        )}
        {loged ? (
          <Route path="/locations" element={<Locations />} />
        ) : (
          <Route path="/locations" element={<Auth />} />
        )}
        {loged ? (
          <Route path="/drivers" element={<Drivers />} />
        ) : (
          <Route path="/drivers" element={<Auth />} />
        )}
        {loged ? (
          <Route path="/users" element={<Users />} />
        ) : (
          <Route path="/users" element={<Auth />} />
        )}
        {loged ? (
          <Route path="/log" element={<Log />} />
        ) : (
          <Route path="/log" element={<Auth />} />
        )}
        {loged ? (
          <Route path="/settings" element={<Settings />} />
        ) : (
          <Route path="/settings" element={<Auth />} />
        )}
      </Routes>
    </HashRouter>
  )

}

export default App