import React, { useRef, useState } from "react";
// import { useNavigate } from "react-router-dom"
import "./Auth.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEye, faEyeSlash } from "@fortawesome/free-regular-svg-icons";

const Eye = <FontAwesomeIcon className="icon text-primary" icon={faEye} />;
const EyeSlash = (
  <FontAwesomeIcon className="icon text-primary" icon={faEyeSlash} />
);

const Auth = (props) => {
  const baseURL = process.env.PUBLIC_URL;
  // const navigate = useNavigate();

  const [formdata, setformdata] = useState({
    email: "",
    password: "",
  });

  const [show, setshow] = useState(true);
  // const [loged, setloged] = useState(false);
  const pass = useRef();

  const { email, password } = formdata;

  const change = (e) => {
    setformdata({ ...formdata, [e.target.name]: e.target.value });
  };

  const submit = (e) => {
    e.preventDefault();
    // Submit to backend API with Axios/fetch
    // Implement your backend logic here.
    localStorage.setItem('isloged', 'yes');
    localStorage.setItem('email', email);
    setformdata({
      email: "",
      password: "",
    });
    // setloged(true)
    // window.location = baseURL + "#/destinations";
    // navigate("/destinations");
    window.location.replace("/destinations");
  };

  const showpassword = () => {
    let chshow = !show;
    setshow(chshow);
    pass.current.type = chshow ? "password" : "text";
  };

  return (
    <>
    <div className="Auth-form-container">
      <form className="Auth-form" onSubmit={submit}>
        <div className="Auth-form-content">
          <h3 className="Auth-form-title">Sign In</h3>
          <div className="form-group mt-3">
            <label>Email address</label>
            <input
              type="email"
              className="form-control mt-1"
              placeholder="Enter email"
              value={email}
              name="email"
              onChange={change}
            />
          </div>
          <div className="form-group mt-3">
            <label className="mb-1">Password</label>
            <div className="input-group">
              <input
                ref={pass}
                type="password"
                className="form-control"
                placeholder="Enter password"
                spellCheck="false"
                autoCorrect="off"
                autoCapitalize="off"
                autoComplete="password"
                value={password}
                name="password"
                onChange={change}
              />
              <span className="input-group-text bg-white">
                {show ? (
                  <i onClick={showpassword}>{Eye}</i>
                ) : (
                  <i onClick={showpassword}>{EyeSlash}</i>
                )}
              </span>
            </div>
          </div>
          <div className="d-grid gap-2 mt-3">
            <button type="submit" className="btn btn-primary">
              Submit
            </button>
          </div>
          <p className="forgot-password text-center mt-2">
            Forgot <a href="#">password?</a>
          </p>
        </div>
      </form>
    </div>
    </>
  );
};

export default Auth;
