const genArrInt = (l) => {
  return Array.from({ length: l }, (value, index) => index + 1);
};

export const sumArrProp = (arr, prop) => {
  const total = arr.reduce((accumulator, object) => {
    let num = parseFloat(object[prop]);
    if (isNaN(num)) {
      num = 0;
    };
    return accumulator + num;
  }, 0);
  return total;
};

export default genArrInt;
